import axios from 'axios'
import dayjs from 'dayjs'
import moment from 'moment'
import { useSnackbar } from 'notistack'
import { type Dispatch, type SetStateAction, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

import AssignmentIndIcon from '@mui/icons-material/AssignmentInd'
import CallIcon from '@mui/icons-material/Call'
import EmailIcon from '@mui/icons-material/Email'
import FactCheckIcon from '@mui/icons-material/FactCheck'
import LocalPoliceIcon from '@mui/icons-material/LocalPolice'
import ShieldIcon from '@mui/icons-material/Shield'
import VerifiedIcon from '@mui/icons-material/Verified'
import {
  Box,
  Button,
  Checkbox,
  Grid,
  type GridProps,
  List,
  ListItem,
  ListItemText,
  MenuItem,
  Modal,
  TextField,
  Tooltip,
  Typography,
  styled,
  useTheme
} from '@mui/material'
import { Stack } from '@mui/system'

import { MEMBER_VALUES } from 'src/data/MemberValues'

import { type ClubId } from 'src/models/club'
import { type ClubMember, type MemberLevel, type Permission } from 'src/models/clubMember'
import { type MemberStatus } from 'src/models/clubMember'

const MEMBERLEVEL_PAGE_LINK = (clubId: number) => {
  return `/manage/${clubId}/settings/member-level`
}

const WITHDRAW = 'WITHDRAW'
const FIRED = 'FIRED'
const PAUSED = 'PAUSED'
const ACTIVE = 'ACTIVE'

const DONE = 'DONE'
const NOTYET = 'NOTYET'

interface UpdateFormValues {
  permission: Permission | ''
  level: MemberLevel | ''
  status: MemberStatus
  fee: 'DONE' | 'NOTYET' | ''
  note: string
}

const getStatus = (member: ClubMember) => {
  if (member.withdrawalAt) return WITHDRAW
  if (member.firedAt) return FIRED
  if (member.isPaused) return PAUSED
  return ACTIVE
}

const ModalContent = styled(Box)(`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 360px;
  background-color: white;
`)

const GridHeaderItem = (props: Partial<GridProps>) => {
  const { sx, ...otherProps } = props
  return (
    <Grid
      item
      xs={6}
      sm={3}
      {...otherProps}
      sx={{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-start',
        alignItems: 'center',
        ...sx
      }}
    />
  )
}

const GridContentItem = (props: Partial<GridProps>) => {
  const { sx, ...otherProps } = props
  return (
    <Grid
      item
      xs={6}
      sm={9}
      {...otherProps}
      sx={{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-end',
        alignItems: 'center',
        wordBreak: 'break-all',
        ...sx
      }}
    />
  )
}

interface DetailsModalProps {
  userPermission: Permission
  targetMembers: ClubMember[]
  setTargetMembers: Dispatch<SetStateAction<ClubMember[]>>
  handleUpdate: (updatedMembers: ClubMember[]) => void
  existingMemberLevels: MemberLevel[]
  clubId: ClubId
}

function DetailsModal({
  userPermission,
  targetMembers,
  setTargetMembers,
  handleUpdate,
  existingMemberLevels,
  clubId
}: DetailsModalProps) {
  const theme = useTheme()

  const { enqueueSnackbar } = useSnackbar()
  moment.locale('ko')

  const [initialValues, setInitialValues] = useState<UpdateFormValues>({
    permission: '',
    level: '',
    status: '',
    fee: '',
    note: ''
  })
  const [updatedValues, setUpdatedValues] = useState<UpdateFormValues>({
    permission: '',
    level: '',
    status: '',
    fee: '',
    note: ''
  })
  const [memberSelection, setMemberSelection] = useState<Array<number>>([])

  const selectedMembers =
    targetMembers.length <= 1 ? targetMembers : targetMembers.filter((_m, i) => memberSelection.includes(i))

  // 변경하기로 선택된 회원 중에서 feeAmount가 없는지 여부 체크
  const hasSelectedLevelFeeAmount = selectedMembers.every(member => {
    return existingMemberLevels.includes(member.level)
  })
  // 선택한 회원 등급의 feeAmount가 없는지 여부 체크
  const hasUpdatedLevelFeeAmount = updatedValues.level && existingMemberLevels.includes(updatedValues.level)
  const canUpdateFee =
    (hasSelectedLevelFeeAmount || hasUpdatedLevelFeeAmount) && (hasUpdatedLevelFeeAmount || updatedValues.level === '')

  const handleModalClose = () => setTargetMembers([])
  const initializeModelSelection = () => setMemberSelection([...Array(targetMembers.length).keys()])
  const handleSelectionChange = (index: number, checked: boolean) => {
    if (checked) {
      setMemberSelection([...memberSelection, index].sort())
    } else {
      setMemberSelection(memberSelection.filter(value => value !== index))
    }
  }
  const handleValuesChange: React.ChangeEventHandler<HTMLInputElement> = event => {
    if (event.target.name === 'note' && event.target.value.length > 200) return

    setUpdatedValues({ ...updatedValues, [event.target.name]: event.target.value })
  }
  const handleSubmit: React.FormEventHandler<HTMLFormElement> = event => {
    event.preventDefault()
    if (targetMembers.length == 0) {
      enqueueSnackbar('수정할 회원이 선택되지 않았습니다', { variant: 'warning' })
      return
    }

    try {
      const updatedMembers = selectedMembers.map(selectedMember => {
        let updatedMember = Object.assign({}, selectedMember)
        if (updatedValues.permission && initialValues.permission !== updatedValues.permission) {
          updatedMember = { ...updatedMember, permission: updatedValues.permission }
        }
        if (updatedValues.level && initialValues.level !== updatedValues.level) {
          updatedMember = { ...updatedMember, level: updatedValues.level }
        }
        if (updatedValues.status && initialValues.status !== updatedValues.status) {
          if (updatedValues.status === WITHDRAW) {
            throw new Error('운영진은 회원의 탈퇴 여부를 변경할 수 없습니다')
          }
          updatedMember = {
            ...updatedMember,
            isPaused: updatedValues.status === PAUSED,
            firedAt: updatedValues.status === FIRED ? dayjs(new Date()).toISOString() : null
          }
        }
        if (updatedValues.fee && initialValues.fee !== updatedValues.fee) {
          updatedMember = { ...updatedMember, fee: updatedValues.fee === DONE }
        } else {
          delete updatedMember.fee
        }
        if (updatedValues.note && initialValues.note !== updatedValues.note) {
          updatedMember = { ...updatedMember, note: updatedValues.note }
        }
        return updatedMember
      })
      handleUpdate(updatedMembers)
    } catch (error) {
      if (axios.isAxiosError(error)) {
        enqueueSnackbar(error?.message, { variant: 'warning' })
      }
    }
  }

  useEffect(() => {
    if (targetMembers.length == 1) {
      const target = targetMembers[0]
      const { permission, level, note } = target
      const status = getStatus(target)
      const fee = target.fee ? DONE : NOTYET
      setInitialValues({
        permission,
        level,
        status,
        fee,
        note
      })
      setUpdatedValues({
        permission,
        level,
        status,
        fee,
        note
      })
    } else {
      setInitialValues({
        permission: '',
        level: '',
        status: '',
        fee: '',
        note: ''
      })
      setUpdatedValues({
        permission: '',
        level: '',
        status: '',
        fee: '',
        note: ''
      })
      initializeModelSelection()
    }
  }, [targetMembers])

  return (
    <Modal
      open={targetMembers.length > 0}
      onClose={handleModalClose}
      aria-labelledby="member-details-modal-title"
      aria-describedby="member-details-modal-description"
    >
      <ModalContent sx={{ boxShadow: 24, padding: 4 }}>
        {/* Modal Title */}
        <Typography variant="h3" sx={{ mb: 2.5, fontSize: '23px', textAlign: 'center' }}>
          {targetMembers.length > 1 ? '회원 동시 수정' : '회원 정보'}
        </Typography>
        {/* Modal Body 1 */}
        {targetMembers.length == 1 && (
          <Grid container spacing={0.2} sx={{ mb: 2 }}>
            <GridHeaderItem xs={12} sm={12} sx={{ mb: 1 }}>
              <Typography variant="h4" sx={{ fontSize: '18px' }}>
                {targetMembers[0].name}
              </Typography>
              {targetMembers[0].isAuthenticated && (
                <Tooltip title="KLUB에서 인증 완료된 회원입니다.">
                  <VerifiedIcon color="primary" fontSize="small" sx={{ marginLeft: '2px' }} />
                </Tooltip>
              )}
              {targetMembers[0].permission == 'SUPERADMIN' && (
                <Tooltip title="최고 운영진 권한을 가진 회원입니다. KLUB에서 제공하는 운영진 기능을 모두 이용할 수 있습니다.">
                  <LocalPoliceIcon color="warning" fontSize="small" sx={{ marginLeft: '2px' }} />
                </Tooltip>
              )}
              {targetMembers[0].permission == 'ADMIN' && (
                <Tooltip title="일반 운영진 권한을 가진 회원입니다. 권한 변경을 제외한, KLUB에서 제공하는 운영진 기능을 모두 이용할 수 있습니다.">
                  <ShieldIcon color="warning" fontSize="small" sx={{ marginLeft: '2px' }} />
                </Tooltip>
              )}
            </GridHeaderItem>
            <GridHeaderItem sx={{ color: theme.colors.alpha.black[70] }}>
              <AssignmentIndIcon sx={{ fontSize: '18px', mr: '2px' }} />
              <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                학번
              </Typography>
            </GridHeaderItem>
            <GridContentItem sx={{ color: theme.colors.alpha.black[70] }}>
              <Typography>{targetMembers[0].studentId}</Typography>
            </GridContentItem>
            <GridHeaderItem sx={{ color: theme.colors.alpha.black[70] }}>
              <EmailIcon sx={{ fontSize: '18px', mr: '2px' }} />
              <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                이메일
              </Typography>
            </GridHeaderItem>
            <GridContentItem sx={{ color: theme.colors.alpha.black[70] }}>
              <Typography>{targetMembers[0].email}</Typography>
            </GridContentItem>
            <GridHeaderItem sx={{ color: theme.colors.alpha.black[70] }}>
              <CallIcon sx={{ fontSize: '18px', mr: '2px' }} />
              <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                전화번호
              </Typography>
            </GridHeaderItem>
            <GridContentItem sx={{ color: theme.colors.alpha.black[70] }}>
              <Typography>{targetMembers[0].phoneNumber}</Typography>
            </GridContentItem>
            <GridHeaderItem sx={{ color: theme.colors.alpha.black[70] }}>
              <FactCheckIcon sx={{ fontSize: '18px', mr: '2px' }} />
              <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                출석
              </Typography>
            </GridHeaderItem>
            <GridContentItem sx={{ color: theme.colors.alpha.black[70] }}>
              <Typography>
                {targetMembers[0].attendance} / {targetMembers[0].totalAttendance}
              </Typography>
            </GridContentItem>
          </Grid>
        )}
        {targetMembers.length > 1 && (
          <>
            <Typography variant="h4" sx={{ color: 'black', fontSize: '18px', mb: 1 }}>
              수정할 회원
            </Typography>
            <List
              sx={{
                width: '100%',
                height: 'fit-content',
                maxHeight: '100px',
                overflowY: 'scroll',
                px: 0,
                py: 0.5,
                border: `1px solid ${theme.colors.alpha.black[10]}`,
                borderRadius: '5px'
              }}
              dense
            >
              {targetMembers.map((targetMember, index) => (
                <ListItem
                  key={`DetailsModal targetMemberSelection ${index}`}
                  secondaryAction={
                    <Checkbox
                      checked={memberSelection.includes(index)}
                      onChange={(_e, c) => handleSelectionChange(index, c)}
                    />
                  }
                  sx={{
                    pl: 1,
                    pr: 0,
                    py: 0.5
                  }}
                  disableGutters
                >
                  <ListItemText
                    primary={`${targetMember.name}`}
                    secondary={`(${targetMember.level}, ${targetMember.studentId})`}
                    primaryTypographyProps={{ display: 'inline' }}
                    secondaryTypographyProps={{ display: 'inline', ml: '3px' }}
                  />
                </ListItem>
              ))}
            </List>
            <Stack direction="row" sx={{ mb: 2, flexWrap: 'wrap', justifyContent: 'space-between' }}>
              <Typography variant="subtitle1" sx={{ fontSize: '12px', ml: '6px' }}>
                총 {targetMembers.length}명 중 {memberSelection.length}명을 수정합니다.
              </Typography>
              <Button size="small" onClick={initializeModelSelection} sx={{ fontSize: '12px', p: 0 }}>
                모두 선택
              </Button>
            </Stack>
          </>
        )}
        {/* Modal Body 2 */}
        <Stack component="form" spacing={1} onSubmit={handleSubmit}>
          <TextField
            id="level"
            name="level"
            label={updatedValues.level !== initialValues.level ? '회원 등급 (변경됨)' : '회원 등급'}
            value={updatedValues.level}
            onChange={handleValuesChange}
            size="small"
            select
            fullWidth
          >
            {targetMembers.length > 1 && <MenuItem value="">변경하지 않음</MenuItem>}
            {MEMBER_VALUES.level.map((level, idx) => (
              <MenuItem key={idx} value={level.display}>
                {level.display}
              </MenuItem>
            ))}
          </TextField>

          <TextField
            id="permission"
            name="permission"
            label={updatedValues.permission !== initialValues.permission ? '운영 권한 (변경됨)' : '운영 권한'}
            value={updatedValues.permission}
            onChange={handleValuesChange}
            disabled={userPermission !== 'SUPERADMIN'}
            size="small"
            select
            fullWidth
          >
            {targetMembers.length > 1 && <MenuItem value="">변경하지 않음</MenuItem>}
            <MenuItem value="SUPERADMIN">최고 운영진</MenuItem>
            <MenuItem value="ADMIN">일반 운영진</MenuItem>
            <MenuItem value="USER">일반 사용자</MenuItem>
          </TextField>
          <TextField
            id="status"
            name="status"
            label={updatedValues.status !== initialValues.status ? '회원 상태 (변경됨)' : '회원 상태'}
            value={updatedValues.status}
            onChange={handleValuesChange}
            disabled={initialValues.status === WITHDRAW}
            helperText={
              initialValues.status == FIRED
                ? `${moment(targetMembers[0]?.firedAt).format('ll')}에 제적한 회원입니다.`
                : initialValues.status == WITHDRAW
                ? `${moment(targetMembers[0]?.withdrawalAt).format('ll')}에 탈퇴한 회원입니다.`
                : null
            }
            size="small"
            select
            fullWidth
          >
            {targetMembers.length > 1 && <MenuItem value="">변경하지 않음</MenuItem>}
            <MenuItem value={ACTIVE}>활동 중</MenuItem>
            <MenuItem value={PAUSED}>휴회원</MenuItem>
            <MenuItem value={FIRED}>제적됨</MenuItem>
            {initialValues.status === WITHDRAW && <MenuItem value={WITHDRAW}>탈퇴함</MenuItem>}
          </TextField>
          <TextField
            id="note"
            name="note"
            label={updatedValues.note !== initialValues.note ? '비고 (변경됨)' : '비고(200자)'}
            value={updatedValues.note}
            onChange={handleValuesChange}
            size="small"
            minRows={1}
            maxRows={4}
            multiline
            fullWidth
          />
          <TextField
            id="fee"
            name="fee"
            label={updatedValues.fee !== initialValues.fee ? '회비 납부 (변경됨)' : '회비 납부'}
            value={updatedValues.fee}
            onChange={handleValuesChange}
            size="small"
            select
            fullWidth
          >
            {targetMembers.length > 1 && <MenuItem value="">변경하지 않음</MenuItem>}
            <MenuItem value={DONE}>납부함</MenuItem>
            <MenuItem value={NOTYET}>미납</MenuItem>
          </TextField>
          {canUpdateFee ? (
            <Box display={'flex'} flexDirection={'row'} justifyContent={'flex-end'} pr={1}>
              <Link to={MEMBERLEVEL_PAGE_LINK(clubId)} style={{ textDecoration: 'none' }}>
                <Typography color="primary">회원등급별 회비 설정</Typography>
              </Link>
            </Box>
          ) : (
            <Box
              sx={{
                borderRadius: '0.75rem',
                padding: 1,
                backgroundColor: theme.colors.secondary.lighter
              }}
            >
              <Typography>
                <Typography fontWeight={'bold'} display={'inline'}>
                  {hasSelectedLevelFeeAmount ? `${updatedValues.level}의 ` : '선택하신 회원 중 일부의 등급이 '}
                </Typography>
                회비가 설정되어 있지 않아 변경이 불가능합니다.&nbsp;
                <Link to={MEMBERLEVEL_PAGE_LINK(clubId)} style={{ textDecoration: 'none' }}>
                  <Typography color="primary" display={'inline'}>
                    여기
                  </Typography>
                </Link>
                서 설정해주세요.
              </Typography>
            </Box>
          )}

          <Stack direction="row" spacing={1} paddingTop={2}>
            <Button variant="outlined" onClick={handleModalClose} fullWidth>
              취소하기
            </Button>
            <Button type="submit" variant="contained" disabled={!canUpdateFee} fullWidth>
              수정하기
            </Button>
          </Stack>
        </Stack>
      </ModalContent>
    </Modal>
  )
}

export default DetailsModal
