import ManageSearchIcon from '@mui/icons-material/ManageSearch'
import PersonRemoveIcon from '@mui/icons-material/PersonRemove'
import SaveAltIcon from '@mui/icons-material/SaveAlt'
import { Button } from '@mui/material'
import {
  type GridRowSelectionModel,
  GridToolbarColumnsButton,
  GridToolbarContainer,
  GridToolbarDensitySelector,
  GridToolbarFilterButton,
  GridToolbarQuickFilter
} from '@mui/x-data-grid'

interface CustomGridToolbarProps {
  isAllSelectedMembersNotBelongToClub: boolean
  selectedMemberIds: GridRowSelectionModel
  handleSearchButtonClick: React.MouseEventHandler<HTMLButtonElement>
  handleFireButtonClick: React.MouseEventHandler<HTMLButtonElement>
  handleDeleteButtonClick: React.MouseEventHandler<HTMLButtonElement>
  handleExportButtonClick: React.MouseEventHandler<HTMLButtonElement>
}

const CustomGridToolbar: React.FC<CustomGridToolbarProps> = ({
  isAllSelectedMembersNotBelongToClub,
  selectedMemberIds,
  handleSearchButtonClick,
  handleFireButtonClick,
  handleDeleteButtonClick,
  handleExportButtonClick
}) => {
  if (selectedMemberIds.length < 1) {
    return (
      <GridToolbarContainer>
        <GridToolbarColumnsButton />
        <GridToolbarFilterButton sx={{ '& svg': { width: '18px', height: '18px' } }} />
        <GridToolbarDensitySelector />
        <Button
          variant="text"
          size="small"
          startIcon={<SaveAltIcon fontSize="small" />}
          onClick={handleExportButtonClick}
        >
          Export
        </Button>
        <GridToolbarQuickFilter />
      </GridToolbarContainer>
    )
  }

  return (
    <GridToolbarContainer>
      <Button
        type="button"
        variant="text"
        size="small"
        startIcon={<ManageSearchIcon fontSize="small" />}
        onClick={handleSearchButtonClick}
      >
        {selectedMemberIds.length === 1 ? 'Inspect' : 'Modify all'}
      </Button>
      {isAllSelectedMembersNotBelongToClub ? (
        <Button
          type="button"
          variant="text"
          size="small"
          startIcon={<PersonRemoveIcon fontSize="small" />}
          onClick={handleDeleteButtonClick}
        >
          {selectedMemberIds.length === 1 ? 'Delete' : 'Delete all'}
        </Button>
      ) : (
        <Button
          type="button"
          variant="text"
          size="small"
          startIcon={<PersonRemoveIcon fontSize="small" />}
          onClick={handleFireButtonClick}
        >
          {selectedMemberIds.length === 1 ? 'Fire' : 'Fire all'}
        </Button>
      )}
    </GridToolbarContainer>
  )
}

export default CustomGridToolbar
