import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import { useMutation, useQuery, useQueryClient } from 'react-query'

import { COMMON_MESSAGE } from 'src/data/messages'

import type { ClubId } from 'src/models/club'
import type { MemberLevel } from 'src/models/clubMember'
import type { DateProps } from 'src/models/date'

interface GetFeeAmountParameters {
  clubId: ClubId
  date: DateProps
  token: string
}
export interface GetFeeAmountResponseData {
  id: number
  level: MemberLevel
  amount: number
}
const getFeeAmountsAPICall =
  ({ clubId, date, token }: GetFeeAmountParameters) =>
  async () => {
    const response = await axios.get<GetFeeAmountResponseData[]>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount?semester=${date.semester}&year=${date.year}`,
      {
        headers: { Authorization: token }
      }
    )
    return response.data
  }
export const useGetFeeAmounts = (clubId: ClubId, date: DateProps) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useQuery<GetFeeAmountResponseData[]>(['feeAmounts', date], getFeeAmountsAPICall({ clubId, date, token }), {
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.SERVER_ERROR
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
      }
    }
  })
}

interface DeleteFeeAmountParams {
  clubId: ClubId
  deleteId: number
  token: string
}
export const deleteFeeAmountAPICall =
  ({ clubId, deleteId, token }: DeleteFeeAmountParams) =>
  async () => {
    await axios.delete<null, AxiosResponse<null>, null>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount/${deleteId}`,
      {
        headers: { Authorization: token }
      }
    )
  }
export const useDeleteFeeAmount = (clubId: ClubId, deleteId: number) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  return useMutation(deleteFeeAmountAPICall({ clubId, deleteId, token }), {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.DELETE_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('feeAmounts')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.DELETE_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

interface PutFeeAmountParams {
  clubId: ClubId
  data: PutFeeAmountRequestData
  editFeeId: number
  token: string
}
export interface PutFeeAmountRequestData {
  amount: number | null
}
const putFeeAmountAPICall =
  ({ clubId, data, editFeeId, token }: PutFeeAmountParams) =>
  async (): Promise<void> => {
    if (data.amount === null) {
      throw Error
    }

    await axios.put<null, AxiosResponse<null>, PutFeeAmountRequestData>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount/${editFeeId}`,
      data,
      {
        headers: { Authorization: token }
      }
    )
  }
export const usePutFeeAmount = (clubId: ClubId, data: PutFeeAmountRequestData, editFeeId: number) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  return useMutation(putFeeAmountAPICall({ clubId, data, editFeeId, token }), {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.UPDATE_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('feeAmounts')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.ADD_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

interface PostFeeAmountParams {
  clubId: ClubId
  date: DateProps
  data: PostFeeAmountRequestData
  token: string
}
export interface PostFeeAmountRequestData {
  level: MemberLevel | null
  amount: number | null
}
const postFeeAmountAPICall =
  ({ clubId, date, data, token }: PostFeeAmountParams) =>
  async () => {
    if (data.amount === null || data.level === null) {
      throw Error
    }

    await axios.post<null, AxiosResponse<null>, PostFeeAmountRequestData>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount?semester=${date.semester}&year=${date.year}`,
      data,
      {
        headers: { Authorization: token }
      }
    )
  }
export const usePostFeeAmount = (clubId: ClubId, date: DateProps, data: PostFeeAmountRequestData) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  return useMutation(postFeeAmountAPICall({ clubId, date, data, token }), {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.ADD_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('feeAmounts')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.ADD_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}
